import app from '@/common/app.js'; //公共变量
import util from '@/common/util.js';
import request from '@/common/request.js';
import {
    mapState,
    mapMutations
} from 'vuex';
export default {
    data() {
        return {
            username: '',
            newphone: '',
            captChaCode: '',
            interval: null, //倒计时函数
            time: '获取验证码', //倒计时
            currentTime: 61,
            disabled: false,
            changephoneDisabled: false,
        }
    },
    onShow() {
        this.username = this.userInfo.sysUser.username;
    },
    computed: {
        ...mapState(['userInfo'])
    },
    methods: {
        ...mapMutations(['setUserInfo']),
        save(event) {
            if (this.newphone.length == 0) {
                this.disabled = false;
                this.showpageToast("请输手机号");
                return false;
            }
            let myphone = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(19[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
            if (!myphone.test(this.newphone)) {
                this.showpageToast("请输入正确的手机号");
                return false;
            }

            if (this.captChaCode.length == '') {
                this.showpageToast("请输入验证码");
                return false;
            } else if (this.captChaCode.length != 6) {
                this.showpageToast("请输入正确的验证码");
                return false;
            }
            //修改密码
            request.http({
                
                url: '/app/changePhone',
                data: {
                    username: this.username,
                    newphone: this.newphone,
                    captChaCode: this.captChaCode
                },
                header: 'application/x-www-form-urlencoded',
                success:(res) =>{
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {
                        this.showpageToast(res.data.msg)
                        this.setUserInfo(res.data.data);
                        this.username = res.data.data.sysUser.username;
                        uni.switchTab({
                            url: "/pages/tabbar/index/index"
                        })
                    }
                    this.showpageToast(res.data.msg)
                },
                fail:()=> {
                    this.showpageToast("请求失败")
                }
            })
        },
        showpageToast(msg) {
            uni.showToast({
                icon: 'none',
                title: msg
            });
        },
        nologin(event) {
            uni.navigateTo({
                url: "./nocard"
            })
        },
        getCode() {
            if (this.newphone.length == 0) {
                this.showpageToast("请输手机号");
                return false;
            }
            let myphone = /^(((13[0-9]{1})|(15[0-9]{1})|(18[0-9]{1})|(19[0-9]{1})|(17[0-9]{1}))+\d{8})$/;
            if (!myphone.test(this.newphone)) {
                this.showpageToast("请输入正确的手机号");
                return false;
            }
            this.currentTime = 61;
            this.disabled = true;
            this.interval = setInterval(() => {
                this.currentTime--;
                this.time = this.currentTime + '秒'
                if (this.currentTime <= 0) {
                    clearInterval(this.interval)
                    this.disabled = false;
                    this.time = '重新发送';
                }
            }, 1000)
            request.http({
                url: "/app/sendChangePhoneCaptCha",
                data: {
                    newphone: this.newphone,
                },
                header: 'application/x-www-form-urlencoded',
                success: (res) => {
                    if (res.data.code == app.RESPONSE_STATUS.REQUEST_SUCCESS) {

                    }else{
                        clearInterval(this.interval);
                        this.time = '获取验证码';
                        this.disabled = false                            
                    }
                    this.showpageToast(res.data.msg);
                },
                fail: (error) => {

                }
            });
        }
    }
}
